
import MHeader from '../components/header/m-header.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
data () {
return {
tabIndex: 0
}
},

components: {
MHeader,
Tab,
TabItem
},

methods: {
switchTabItem(index) {
switch (index) {
case 0:
this.$router.push('/')
break
case 1:
this.$router.push('/singers')
break
case 2:
this.$router.push('/rank')
break
case 3:
this.$router.push('/searchSongs')
break
default:
this.$router.push('/recommend')
}
this.$vux.loading.show()
setTimeout(() => {
this.$vux.loading.hide()
this.tabIndex = index
}, 200)
}
},

computed: {},
created() {},
mounted() {
this.$nextTick(() => {
let path = this.$route.name
switch (path) {
case 'recommend':
this.tabIndex = 0
break
case 'singers':
this.tabIndex = 1
break
case 'rank':
this.tabIndex = 2
break
case 'searchSongs':
this.tabIndex = 3
break
default:
this.tabIndex = 0
}
})
}
}




